.TransactionListItem {
    /* gap: 160px; */
    position: relative;
    display: flex;
    align-items: center;

    padding: 12px;
    margin-bottom: 5px;

    border: 1px solid #000000;
    border-radius: 4px;
    
    background-color: #c7c7c7;
}


.TransactionListItemText {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    /* margin-right: 24px; */
    margin-left: 24px;

    font-size: 20px;
    font-weight: 700;

    color: #000872;
}

.TransactionListItemTextSumExpenses {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    /* margin-right: 24px; */
    margin-left: 24px;

    font-size: 20px;
    font-weight: 700;

    color: #b30000;
}

.TransactionListItemTextSumIncome {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    /* margin-right: 24px; */
    margin-left: 24px;

    font-size: 20px;
    font-weight: 700;

    color: #239400;
}

.ContactListNumber {
    color: #000c75;
}


.ContactListEditBtn {
    border: none;
    font: inherit;
    cursor: pointer;
    outline: none;

    margin-left: auto;
    margin-right: 24px;
    border-radius: 4px;
    padding: 12px 24px;
    background-color: #006655;
    color: #ffffff;
    font-weight: 500;

    box-shadow: 0px 4px 4px #00000026;
}

.ContactListEditBtn:hover,
.ContactListEditBtn:focus {
    background-color: #00b38c;
}

.ContactListDeleteBtn {
    border: none;
    font: inherit;
    cursor: pointer;
    outline: none;

    /* margin-left: auto; */
    border-radius: 4px;
    padding: 12px 24px;
    background-color: #810000;
    color: #ffffff;
    font-weight: 500;

    box-shadow: 0px 4px 4px #00000026;
}
.ContactListDeleteBtn:hover,
.ContactListDeleteBtn:focus {
    background-color: #be0000;
}

